import * as React from 'react'
import Layout from '../components/layout'
import { StaticImage } from 'gatsby-plugin-image'
import { Container } from 'react-layout-components';
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button"
import { graphql } from 'gatsby'

const BoardOfDirectorsPage = () => {
  return (
    <Layout pageTitle="Board Of Directors">
      <h1>2021 Board of Directors</h1>
      <Container>
      <Card style={{ width: '18rem' }}>
        <Card.Img variant="top" src="../images/board_of_directors/moises_young.jpg" />
        <Card.Body>
          <Card.Title>President</Card.Title>
          <Card.Text>
            Some quick example text to build on the card title and make up the bulk of
            the card's content.
          </Card.Text>
          <Button variant="primary">Go somewhere</Button>
        </Card.Body>
      </Card>
      </Container>

    </Layout>
  )
}

// graphql querys need to be done outside of page component
export const query = graphql`
    query {
        allFile {
            nodes {
                name
            }
        }
    }
`

export default BoardOfDirectorsPage